.dish-preview {

    .dish-preview-container {
        width: 540px;
        height: 96px;

        img {
            width: 160px;
            height: 96px;
            margin-right: 62px;


        }

        .hr-orange {
            position: absolute;
            right: 30px;
            top: 12px;
            border-color: #9E7F66;
            width: 32px;
        }

        .txt-container {
            width: 350px;
            height: 88px;
            line-height: 28px;

            h3 {
                font-size: 18px;
            }

            p {
                font-size: 15px;
            }
        }


    }

    @media (max-width:1430px) {
        width: 100%;
        margin-bottom: 49px;

        .dish-preview-container {
            align-items: center;
            flex-direction: column;
            width: 100%;
            height: 100%;

            .img-container {
                width: 100%;
                height: 245px;
                margin-bottom: 32px;

                img {
                    width: 100%;
                    height: 100%;
                    margin: 0;
                }
            }
        }
    }

}